<style lang="scss">
  @import "../../sass/baseSass.scss";
  #customized-index{
    background: #f0f0f0;

    .list-item{
      background: #fff;
      width: box(750);
      margin-bottom: box(20);
      padding: 0 box(10) box(30);

      h3{
        font-size: box(32);
        color: #323232;
        font-weight: 800;
        padding: box(30);
      }
      .item-detail{
        width: box(360);
        height: box(200);
        background: #fc4518;
        float: left;
        position: relative;
        overflow: hidden;
        margin-bottom: box(10);

        img{
          width: 100%;
          heihgt: 100%;
          position: absolute;
          top: 0;
          left: 0;
        }
      }
      .item-detail:nth-child(even){
        margin-right: box(10);
      }
      .mr10{
        margin-right: box(10);
      }
      .clear{
        clear: both;
      }
    }

  }

</style>



